.container {
  display: grid;
  grid-column: span;
  grid-row: span;
  grid: auto auto;
  /* grid-gap: 30px; */
  min-height: 100vh;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
